.card{
    display: flex;
    flex-direction: column;
    border-radius: 25px;
    overflow: hidden;
    box-shadow: 0px 0px 25px 3px rgba(39,194,124,1);

    &_link{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgba(28, 230, 196,0.8);
    }

    &_info{
        margin-left: 100px;
        margin-right: 100px;
    }

    &_img{
        background-color: rgba(28, 230, 196,0);
        margin-top: 10px;
        &-cont{
            border-radius: 50px
        }
    };
    &_name{
        font-size: 26px;
        padding: 10px;
        background-color: rgba(28, 230, 196,0.0);
        color: black;
        font-family: 'Hanalei Fill', cursive;
    };
    &_species{
        font-size: 20px;
        margin-bottom: 15px;
        background-color: rgba(28, 230, 196,0.0);
        color: black;
        font-weight: 700;
        font-family: 'Amatic SC', cursive;

    };
};

